import React from 'react'

const ReferModal = () => {
  return (
    <>
       <div class="offcanvas offcanvas-end editProbationReview" tabindex="-1" id="editProbationReviews">
        <div class="offcanvas-header">
            <h5 class="offcanvas-title  fs-5 fw-bold text-primary">Candidate Details</h5>
            <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
        </div>
        <div class="offcanvas-body">
            <div class="probationForm">
                <div class="formPanel referNowform">
                    <div class="row">
                        <div class="col-md-6">
                            <div class="form-group mb-3">
                                <label>First Name</label>
                                <input type="text" class="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group mb-3">
                                <label>Last Name</label>
                                <input type="text" class="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group mb-3">
                                <label>Email *</label>
                                <input type="email" class="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group mb-3">
                                <label>Mobile Number *</label>
                                <input type="number" class="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group mb-3">
                                <label>Designation *</label>
                                <input type="text" class="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group mb-3">
                                <label>DOB *</label>
                                <input type="text" class="form-control" placeholder="Enter here"/> 
                            </div>
                        </div>
                        <div class="col-md-12">
                            <div class="form-group mb-3">
                                <label>Full Address *</label>
                                <input type="text" class="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group mb-3">
                                <label>Pincode *</label>
                                <input type="text" class="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group mb-3">
                                <label>Experience *</label>
                                <input type="text" class="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group mb-3">
                                <label>Technology Knowledge *</label>
                                <input type="text" class="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group mb-3">
                                <label>Resume *</label>
                                <div class="position-relative">
                                    <input type="text" class="form-control" value="Choose File" />
                                    <div class="inputuploader"><img src="images/file-input.svg" alt="" width="26"/>
                                        <input type="file" name="upload-img" accept="image/*" /></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="d-flex align-items-center justify-content-end gap-2 border-top pt-3">
                <button type="button" class="btn btn-org-gradient" data-bs-toggle="modal"
                    data-bs-target="#otpModal">Submit</button>
                <button type="button" class="btn btn-secondary">Back</button>
            </div>
        </div>
    </div>
    </>
  )
}

export default ReferModal
